    'antibiogram-trend': () =>
      import(/* webpackChunkName: "antibiogram-trend" */ "@/components/scoped/antibiogram-trend"),
  },
  computed: {
    ...mapGetters(['antibiogramFilter', 'gramPositive', 'gramNegative', 'others']),
    comp() {
      return this.component
    },
  },
  data() {
    return {
      viewAntibiogramFilter: false,
      dialog: false,
      active: false,
      showToolTip: false,
      tabs: ["Table", "Chart","Trend"],
      component: "antibiogram-table"
    }
  },
  methods: {
    scopeItem(item) {
      if (this.antibiogramFilter.scope === item) {
        return true
      }
      return false
    },
    changeComp(n) {
      switch (n) {
        case 0:
          this.component = "antibiogram-table";
          break;
        case 1:
          this.component = "antibiogram-chart";
          break;
        case 2:
          this.component = "antibiogram-trend";
          break;
      }
    },
    viewAntibiogram() {

    },
    gotoPathogen(pathogen) {
      this.$router.push(`pathogens/${pathogen}`)
    }
  }
};
